<script>
import SetUpCrmModalPopup from './SetUpCRM.vue';
import DataService from '../../../services/DataService'
export default{
   components:{
       SetUpCrmModalPopup
   },
   data(){
    return{
        showSetUpCrmModalPopup:false,
        popupCondition:'',
        CompanyType:'',
        SetupProgress:'',
        moduleId:0,
        windowHeight: window.innerHeight,
        progressColor:''
    }
   },
   created:async function(){
        var vm=this;
        vm.CheckLocateOrNonLocate();    
    },
   methods:{
    CheckLocateOrNonLocate:function(){
            var vm=this;
            DataService.IsExtraMapFeature().then(function (response) {
                vm.CompanyType=response.data;
                vm.GetStorageSetupStatusDetail();
            });
    },
    GetStorageSetupStatusDetail: function () {
            var vm = this;
            var url=`companyType=${vm.CompanyType}`
            DataService.GetStorageSetupStatusDetail(url).then(function (response) {
                if (response.data != null) {
                    var res = JSON.parse(response.data);
                    vm.SetupProgress = parseInt(res.data[0].TOTAL_PROGRESS);
                    vm.moduleId=parseInt(res.data[0].MODULEID)
                    if(vm.windowHeight<568){
                         if(vm.SetupProgress==25)
                        {
                            vm.progressColor="progress-bar progress-bar-striped bg-danger"
                        }
                        else if(vm.SetupProgress==50){
                            vm.progressColor="progress-bar progress-bar-striped bg-warning"
                        }
                        else if(vm.SetupProgress==75){
                            vm.progressColor="progress-bar progress-bar-striped bg-info"
                        }
                        else if(vm.SetupProgress==100){
                            vm.progressColor="progress-bar progress-bar-striped bg-success"
                        }
                    }
                    else{
                    if(vm.SetupProgress==25){
                        vm.progressColor='red'
                    }
                    else if(vm.SetupProgress==50)
                    {
                        vm.progressColor='yellow'
                    }
                    else if(vm.SetupProgress==75)
                    {
                        vm.progressColor='blue'
                    }
                    else if(vm.SetupProgress==100)
                    {
                        vm.progressColor='green'
                    }
                    }
                }
            });
        },
    ShowSetCRMPopup:function(param){
        var vm=this;
        vm.showSetUpCrmModalPopup=true;
        vm.popupCondition=param
    },
    Close:function(){      
        var vm=this;
        vm.showSetUpCrmModalPopup=false;
    },
    RedirectToLinks: function(redirectType){        
        var envLink=process.env;                
        if(redirectType=="Feedback")
        {
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Feedback/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="IndustryType"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/IndustryType/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="ProductType"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/CRM/ItemTypeListing'
            window.open(link);
            return false
        }
        else if(redirectType=="Channel"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Channel/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="Category"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Category/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="CustomFields"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Custom/fields'
            window.open(link);
            return false
        }
        else if(redirectType=="ManageLayout"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Custom/Layouts'
            window.open(link);
            return false
        }
        else if(redirectType=="EmailTemplate"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/EmailTemplate/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="Clients"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Clients/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="ManageTax"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Tax/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="Status"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Status/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="CRMRoleType"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/Roles/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="CurrencyManagement"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/CurrencyManagement/Index'
            window.open(link);
            return false
        }
        else if(redirectType=="ContactType"){            
            var link=envLink.VUE_APP_CURRENT_SITE_ROOT_URL+'/ContactType/Index'
            window.open(link);
            return false
        }
        
    }
   }
}
</script>
